<template>
  <div class="modal fade" :id="id_prefix + 'Modal'" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">
            {{ title }}
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body" v-html="txt">
          
        </div>
        <div class="modal-footer">
          <button v-if="need_cancel" type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-main" @click="sureDone">

            <slot name="submit_text"></slot>
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import tool from '@/common/es/tool'
export default {
  name: 'SubmitModal',
  props: {
    need_cancel: {
      type: Boolean,
      default: true
    },
    id_prefix: {
      type: String,
      default: 'custom'
    },
    txt: {
      type: String
    },
    title: {
      type: String
    }
  },
  methods: {
    sureDone() {
      tool.closeModal(this.id_prefix + 'Modal')
      this.$emit('done_Father')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


