import { net } from '@/service/api/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

let url = config.baseURL + config.apiURL + '/listing_content/'
const _url = url 

export default {
  url,
  getList_ListingContent(condition, status) {

    url = _url + conn_util.buildURL(condition, status)
    return net({
      url,
      method: 'get'
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data
    })
  },
  getOne(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'GET'
    })
  },
  getOneByListing(condition) {
    url = _url + conn_util.buildURL(condition, true)
    
    return net({
      url,
      method: 'get'
    })
  }
}


