const paymetList = [
  '支票',
  '現金'
]

module.exports = {
  // 
  ser_unit (unit) {

    if(unit == 0) {
      return '斤'
    } else if (unit == 1) {
      return '包'
    } else if (unit == 2) {
      return '件'
    } else if (unit == 3) {
      return '扎'
    } else if (unit == 4) {
      return '两'
    } else if (unit == 5) {
      return '磅'
    } else if (unit == 6) {
      return '条'
    }

    if (unit == -1) {
      return '未选择'
    }
  },
  //
  ser_time (timed) {
    if ((timed == '') || (timed == '') || (timed == '')) {
      return '-'
    }
    return timed
  },
  //
  ser_prices_status(draft_status, over_status) {
    if (draft_status) {
      return '<div class="draft">草稿</div>'
    }
    if (over_status) {
      return '已结款'
    }
    return '未结款'
  },
  //
  ser_prices_status_pdf(first_pdf, _html_no, _html_yes) {
    if (first_pdf) {
      return _html_no
    }
    return _html_yes
  },
  //
  show_paymet() {
    return paymetList
  },
  ser_listing_paymet(index) {
    return paymetList[index]
  }
}


