<template>
  <div>

    <div class="fw-b" :class="`fs-${fs_type}-36`">{{ pdf_title }}</div>
    <div :class="`fs-${fs_type}-22`">
      {{ pdf_sub }}
    </div>
    <div :class="`fs-${fs_type}-20`">
      电话：{{ pdf_contact_phoned }}&nbsp;&nbsp;传真：{{ pdf_contact_fax }}
    </div>
    <div :class="`fs-${fs_type}-20`">
      电邮地址：
    </div>
    <div class="mt-2 fw-b" :class="`fs-${fs_type}-30`">
      <slot></slot>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'PdfHeader',
  props: {
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  data() {
    return {
      pdf_title: 'Lumicrest International Limited',
      pdf_sub: 'Office: Rm A12, 8/F Lee King Industrial Building 12 Ng Fong Street, San Po Kong, Kowloon, Hong',
      pdf_contact_phoned: '5488 1979 5424 4119',
      pdf_contact_fax: '2777 0760',
    }
  }
}
</script>


<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
  
</style>


