<template>
  <table class="pdf-table w-100" :class="`fs-${fs_type}-18`">
    <tbody v-if="member != null">
      <tr>
        <td width="45%">报价单号：</td>
        <td>{{ pricesNum }}</td>
      </tr>
      <tr>
        <td>客户编号：</td>
        <td>{{ member.num }}</td>
      </tr>
      <tr>
        <td>报价时间：</td>
        <td>{{ pricesTime }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'PdfPriceMsg',
  props: {
    pricesTime: {
      type: String
    },
    pricesNum: {
      type: String,
      default: ''
    },
    member: {
      type: Object
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  data() {
    return {
      
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
</style>


