<template>
  <table class="w-100 pdf-table" :class="`fs-${fs_type}-18`">
    <tbody v-if="member != null">
      <tr>
        <td colspan="3">&nbsp;</td>
      </tr>
      <tr>
        <td width="14%">客户：</td>
        <td colspan="2">{{ member.named }}</td>
      </tr>
      <tr>
        <td>地址：</td>
        <td colspan="2">{{ member.addr }}</td>
      </tr>
      <tr>
        <td colspan="3">&nbsp;</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'PdfPriceMember',
  props: {
    member: {
      type: Object
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
</style>


