
export default {
  buildURL(condition, status, ordering = '-add_time') {
    let res = '?format=json&ordering=' + ordering + '&'
    
    if (JSON.stringify(condition) == '{}') {
      
    } else {

      for (const k in condition) {
        const item = k + '=' + condition[k] + '&'
        res += item
      }

    }

    if (status) {
      res += 'status=True'
    } else {
      res += 'status='
    }

    return res
  },
  getAuth() {
    return {
      username: 'VcrTing',
      password: 'ZT123zlt'
    }
  }
}


