const buildTitle = function(txtList) {
  return `关于${txtList}的帮助`
}

const buildHtml = function(txtList) {
  let html = `<ul style="font-size: 16px">`

  for (const i of txtList) {
    html +=
    `<li class="mb-2">${i}</li>`
  }
  html += `</ul>`

  return html
}

const helpHtmlMap = {
  'prices_input': buildHtml([
    '1. 每一个菜品对应一个单价与一个价格单位',
    '2. 单价的值可以随意修改，但是价格单位无法修改',
    '3. 你可以在每一个菜品的单价处输入任何数字以用于定义该菜品的单价',
    '4. 输入后可按回车，也可点击网页任意地方，输入框会自动保存你的输入',
    '5. 祝事业顺利！'
  ]),
  'listing_search': buildHtml([
    '1. 可以使用搜索功能搜索你想要的发票',
    '2. 目前只开放与 发票编号、报价单号、客户编号、客户名称 有关的词条搜索',
    '3. 你可以在搜索框中输入任何内容',
    '4. 按搜索框右边的 清空 按钮，可以将左边的内容快速清空',
    '5. 当超过一个搜索框内有内容时，搜索框内容均生效',
    '6. 祝事业顺利！'
  ]),
  'listing_plus_num_input': buildHtml([
    '1. 数量，即一个菜品的订购量',
    '2. 每一项菜品的数量的值可以随意修改',
    '3. 数量的计量单位取自于菜品的计量单位，无法在这里修改',
    '4. 输入后可按回车，也可点击网页任意地方，输入框会自动保存你的输入',
    '5. 祝事业顺利！'
  ]),
  'listing_plus_price_input': buildHtml([
    '1. 单价，即一个菜品单位价格',
    '2. 单价的值可以随意修改',
    '3. 单价修改后不会影响 原来的菜品的默认单价，修改的值只作用于当前的发票中的菜品',
    '4. 输入后可按回车，也可点击网页任意地方，输入框会自动保存你的输入',
    '5. 祝事业顺利！'
  ]),
  'listing_plus_discount_input': buildHtml([
    '1. 折扣，即给每个菜品价格减去多少钱',
    '2. 只需输入想要的数值，该菜品的价格的值会自动减去这个值',
    '3. 当前版本还不支持折扣功能，如需使用折扣功能，请联系开发人员',
    '4. 祝事业顺利！'
  ])
}

const helpTitleMap = {
  'prices_input': buildTitle('报价单价'),
  'listing_search': buildTitle('发票的搜索功能'),
  'listing_plus_num_input': buildTitle('发票的菜品数量'),
  'listing_plus_price_input': buildTitle('发票的菜品单价'),
  'listing_plus_discount_input': buildTitle('发票的折扣')
}

const getHtml = function(help_code) {
  return helpHtmlMap[help_code]
}

const getTitle = function(help_code) {
  return helpTitleMap[help_code]
}

export default {
  getTitle,
  getHtml
}


