<template>
  <div class="btn btn-light btn-sm help-icon" @click="openHelpModal">
    ?
  </div>
</template>

<script>
import $ from 'jquery'
import tool from '@/common/es/tool'
import lang from '@/common/es/help/help_lang.js'

export default {
  name: 'HelpIcon',
  props: {
    help_code: {
      type: String
    }
  },
  methods: {
    openHelpModal() {
      const title = lang.getTitle(this.help_code)
      const html = lang.getHtml(this.help_code)

      $('#helpModalTitle').html(title)
      $('#helpModalBody').html(html)
      
      $('#helpModal').modal('show')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
.help-icon
  display: inline
  width: 30px !important
  height: 28px !important
  line-height: 1
  border-radius: 100%
</style>


